@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&family=Open+Sans:wght@400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Arvo:ital,wght@0,400;0,700;1,700&family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,100;1,200;1,300&family=Open+Sans:wght@400;500;600;700&display=swap");
body {
  margin: 0;
  background-color: rgb(0, 17, 34);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.logo-img {
  max-height: 100%;
}

.hero-container {
  padding-top: 5vh;
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 100%;
}
.centering {
  padding-top: 15vh;
}
.hero-text {
  flex-grow: 1;
}
.hero-img {
  flex-grow: 1;
  /* height: 100%; */
  justify-self: start;
  align-items: center;
}

p.text1 {
  color: white;
  font-family: sans-serif;
  margin: 0;
  padding-bottom: 1vh;
  /* padding-left: 1vh; */
  font-size: 3.2vh;
  font-weight: 700;
  letter-spacing: 0.5vh;
}
p.text2 {
  color: #ffe350;
  /* font-size: 6.5vh; */
  font-size: 40px;
  margin: 0;
  font-weight: 700;
  font-family: "Open Sans";
}
p.text3 {
  /* font-family: "Libre Baskerville"; */
  color: white;
  /* font-size: 15vh; */
  padding: 0;
  margin: 0;
  /* font-weight: 600; */
  font-family: "Libre Baskerville";
  font-size: 84px;
  font-weight: 700;
  line-height: 104px;
  letter-spacing: 0em;
  text-align: left;
}
p.text4 {
  color: #ffe350cc;
  margin: 0;
  padding: 0;
  /* padding-left: 1vh; */
  padding-bottom: 2vh;
  font-size: 3.7vh;
  font-size: 20px;
  padding-top: 5px;
  letter-spacing: 0.3vh;
  font-weight: 400;
}
p.text5 {
  color: white;
  margin: 0;
  padding: 0;
  /* padding-left: 1vh; */
  padding-bottom: 4vh;
  /* font-size: 3.7vh; */
  font-size: 20px;

  padding-top: 5px;
  font-weight: 400;
  letter-spacing: 0.3vh;
}
.hero-text button {
  font-weight: 800;
  font-size: 2.2vh;
  padding: 2.6vh;
  border-radius: 15px;
}
