form input[type="text"],
input[type="password"] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

form button {
  background-color: #04aa6d;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 100%;
}

form button:hover {
  opacity: 0.8;
}

form .cancelbtn {
  width: auto;
  padding: 10px 18px;
  background-color: #f44336;
}

form .imgcontainer {
  text-align: center;
  margin: 24px 0 12px 0;
  position: relative;
}

form img.avatar {
  width: 20%;
  border-radius: 100%;
  padding-top: 20px;
}

form .container {
  padding: 16px;
}

form span.psw {
  float: right;
  padding-top: 16px;
}

.modal {
  position: fixed;
  z-index: 20;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.524);
  padding-top: 60px;
}

.modal-content {
  background-color: #000821e4;
  margin: 5% auto 15% auto;
  border: none;
  color: #ffe350;
  width: 80%;
  padding: 20px;
  border-radius: 30px;
  border: 1px solid #ffe350;
}
.modal-content label {
  font-size: 24px;
}
@media (max-width: 6000px) {
  .modal-content {
    width: 65%;
  }
}
@media (max-width: 680px) {
  .modal-content {
    width: 70%;
  }
}
@media (max-width: 400px) {
  .modal-content {
    width: 80%;
  }
}

.close {
  position: absolute;
  right: 25px;
  top: 0;
  color: #000;
  font-size: 35px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: red;
  cursor: pointer;
}
.animate {
  -webkit-animation: animatezoom 0.6s;
  animation: animatezoom 0.6s;
}

@-webkit-keyframes animatezoom {
  from {
    -webkit-transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
  }
}

@keyframes animatezoom {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}
@media screen and (max-width: 300px) {
  span.psw {
    display: block;
    float: none;
  }
  .cancelbtn {
    width: 100%;
  }
}
